.navbar ul{
  display: flex;
  background-color: #68f0fc;
  margin: 0;
  
}

.navbar li {
  list-style-type: none; /* Remove bullets */
  padding: 25px;
  margin: 0;
  font-size: 20px;
}

.navbar a {
  text-decoration: none;
  color: rgb(0, 65, 16);
  font-weight: 500;
}

.footer {
  background-color: #68f0fc;
  display: flex;
  justify-content: space-between;
  padding: 10px;
  align-items: center;
}

.footer img {
  height: 27px;
}

.logos {
  display: flex;
  gap: 20px;
  padding-right: 50px;
}